.btn {
  border-radius: 50%;
  height: 32px;
  width: 100px;
  /* position: absolute; */
  border: none;
  bottom: 10%;
  color: rgb(255, 255, 255);
  font-size: 24px;
  cursor: pointer;
  /* font-family: FontAwesome; */
  outline: none;
}



